import { Component, OnInit, OnDestroy } from '@angular/core';
import { LoadingController, NavController, AlertController } from '@ionic/angular';
import { BasePagePage } from 'src/app/shared/base-page/base-page.page';
import { BaseService } from 'src/app/services/base.service';
import * as moment from 'moment';
import { Subscription, timer } from 'rxjs';
import { Constants, StatusCode } from 'src/app/data-model/constant.model';
import { FormatTimePipe } from 'src/app/data-model/date.pipe';
import { Storage } from '@ionic/storage';
import { DownModel, AccessTokenModel, SMSModel, InsertMDownDetailModel, ProblemModel } from 'src/app/data-model/base.model';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-machine-down',
  templateUrl: './machine-down.page.html',
  styleUrls: ['./machine-down.page.scss'],
})
export class MachineDownPage extends BasePagePage implements OnInit, OnDestroy {

  time: Date;
  prevNowPlaying: any;
  arrRecord: Array<any> = [];
  countDown: Subscription;
  counter = 0;
  tick = 1000;

  // machineDownReasonReqModel: DownModel = new AccessTokenModel();
  arrDownReasons: Array<any> = [];
  checkBoxList: Array<any> = [];

  problemReqModel: ProblemModel = new ProblemModel();

  isIndeterminate: boolean;
  masterCheck: boolean;
  downStartTime: any;
  machineDownID: string;
  transfile: any;

  downstart: any;
  smsReqModel: SMSModel = new SMSModel();
  insertMachineDownReqModel: InsertMDownDetailModel = new InsertMDownDetailModel;

  constructor(
    public loadingCtrl: LoadingController,
    public alertCtrl: AlertController,
    public navCtrl: NavController,
    private service: BaseService,
    private formatTimePipe: FormatTimePipe,
    private storage: Storage,
    private route: ActivatedRoute
  ) {

    super(loadingCtrl, alertCtrl);

  }

  ngOnInit() {

    if (this.route.snapshot.data['special']) {

      this.downstart = this.route.snapshot.data['special'];
      this.machineDownID = this.downstart.machineDown.MachineDownID;
      this.counter = this.downstart.machineDown.TimeStamp;
      this.downStartTime = this.downstart.machineDown.StartTime;
      this.countDown = timer(0, this.tick).subscribe(() => ++this.counter);

      this.smsReqModel = this.downstart.sms;

      this.insertMachineDownReqModel.machinedownid = this.machineDownID;
      this.insertMachineDownReqModel.machineid = this.smsReqModel.machineid;

      this.transfile = this.downstart.transfile;

      this.problemReqModel.processcode = this.downstart.transfile.ProcessCode;

    }

    this.storage.get(Constants.c_ACCESS_TOKEN).then((accessToken) => {

      this.problemReqModel.accesstoken = accessToken;

      this.insertMachineDownReqModel.accesstoken = accessToken;

      this.service.presentLoading();

      this.service.callWebService(Constants.k_GET_MACHINE_DOWN_PROBLEM, this.problemReqModel, (res) => {

        if (res.status_code == StatusCode.Success) {

          this.arrDownReasons = JSON.parse(res.data);

          this.arrDownReasons.forEach(element => {

            let machine = {
              'name': element.name,
              'value': element.value
            }

            this.checkBoxList.push({ 'machine': machine, 'isChecked': false })

          });

          this.service.dismissLoading();

        } else {

          this.service.dismissLoading();

        }

      });

    });

  }

  ngOnDestroy() {
    this.countDown = null;
  }

  checkMaster() {
    setTimeout(() => {
      this.checkBoxList.forEach(obj => {
        obj.isChecked = this.masterCheck;
      });
    });
  }

  checkEvent() {
    const totalItems = this.checkBoxList.length;
    let checked = 0;
    this.checkBoxList.map(obj => {
      if (obj.isChecked) checked++;
    });
    if (checked > 0 && checked < totalItems) {
      //If even one item is checked but not all
      this.isIndeterminate = true;
      this.masterCheck = false;
    } else if (checked == totalItems) {
      //If all are checked
      this.masterCheck = true;
      this.isIndeterminate = false;
    } else {
      //If none is checked
      this.isIndeterminate = false;
      this.masterCheck = false;
    }
  }

  readable(val) {
    return val.toString().match(/\d/g).join("");
  }

  onMachineDownClicked(form, e: Event) {

    let totalTime = this.formatTimePipe.transform(this.counter);
    let arrSplit = totalTime.split(":");
    let hours = Number(arrSplit[0]);
    let minutes = Number(arrSplit[1]);
    let seconds = Number(arrSplit[2]);

    console.log("hours: ", hours);
    console.log("minutes: ", minutes);
    console.log("seconds: ", seconds);

  }

  onSubmitClicked(form, e: Event) {

    this.insertMachineDownReqModel.remark = [];
    this.insertMachineDownReqModel.problemid = [];

    this.checkBoxList.forEach(element => {
      if (element.isChecked) {
        this.insertMachineDownReqModel.remark.push(element.machine.name);
        this.insertMachineDownReqModel.problemid.push(element.machine.value);
      }
    });

    BasePagePage.presentAlert('Alert', 'Are you sure want to submit?<br/><br/>' + this.transfile.Machine + ' Down Reasons:<br/>- ' + this.insertMachineDownReqModel.remark.join('<br/>- '), (res) => {

      if (res.role == Constants.b_OK) {

        this.service.presentLoading();

        this.service.callWebService(Constants.k_GET_INSERT_MDOWN_DETAIL, this.insertMachineDownReqModel, (res) => {

          if (res.status_code == StatusCode.Success) {

            this.service.dismissLoading();

            BasePagePage.presentAlert(res.status_desc, res.data, (response) => {

              if (response) {

                this.navCtrl.navigateBack('/operator')
                
              }

            });

          } else {

            this.service.dismissLoading();

          }

        });

      }

    }, [Constants.b_CANCEL, Constants.b_OK]);

  }

  onCallForHelpClicked() {

    BasePagePage.presentAlert('Alert', 'Are you sure want to request assistance from your supervisor?', (res) => {

      if (res.role == Constants.b_OK) {

        this.service.presentLoading();

        this.service.callWebService(Constants.k_SEND_SMS, this.smsReqModel, (res) => {

          if (res.status_code == StatusCode.Success && BasePagePage.presentAlert(res.status_desc, res.data)) {

            this.service.dismissLoading();

          } else {

            this.service.dismissLoading();

          }

        });

      }

    }, [Constants.b_CANCEL, Constants.b_OK]);

  }

}